:root {
  --clr-header: hsl(217, 19%, 35%);
  --clr-username: hsl(217, 19%, 35%);
  --clr-intro: hsl(214, 17%, 51%);
  --clr-date: hsl(212, 23%, 69%);
  --clr-background: hsl(210, 46%, 95%);
}

:root {
  --ff-primary: "Manrope", sans-serif;
}

:root {
  --fs-400: 0.8125rem;
  --fs-700: 1.2rem;
}

@media screen and (max-width: 39em) {
  :root {
    --fs-400: 0.79rem;
    --fs-700: 0.95rem;
  }
}
:root {
  --fw-medium: 500;
  --fw-semibold: 700;
}

