/*** box sizing border-box for all elements ***/
*,
*::before,
*::after {
  box-sizing: border-box;
}

a,
a:visited,
a:active {
  text-decoration: none;
  color: inherit;
  cursor: pointer;
}

button {
  background-color: transparent;
  color: inherit;
  border-width: 0;
  padding: 0;
  cursor: pointer;
}

figure {
  margin: 0;
}

input::-moz-focus-inner {
  border: 0;
  padding: 0;
  margin: 0;
}

ul,
ol,
dd {
  margin: 0;
  padding: 0;
  list-style: none;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  margin: 0;
  font-size: inherit;
  font-weight: inherit;
}

p {
  margin: 0;
}

cite {
  font-style: normal;
}

fieldset {
  border-width: 0;
  padding: 0;
  margin: 0;
}

body {
  font-size: var(--fs-400);
  font-family: var(--ff-primary);
  min-height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center;
  background-color: var(--clr-background);
}

.main__intro p {
  color: var(--clr-intro);
}
.main__intro > p:first-child {
  font-size: var(--fs-700);
  font-weight: var(--fw-semibold);
  color: var(--clr-header);
}

.side__user-name {
  font-weight: var(--fw-semibold);
  color: var(--clr-username);
}
.side__date {
  color: var(--clr-date);
}

.main {
  max-width: 50em;
  width: 80vw;
  display: grid;
  grid-template-columns: 250px 1fr auto;
  grid-template-rows: 1em 1fr auto 1em;
  background-color: white;
  gap: 15px 2em;
  border-radius: 10px;
  box-shadow: 3px 4px 45px -20px gray;
  line-height: 1.3;
  position: relative;
}
.main__image {
  grid-row: 1/-1;
  overflow: hidden;
  border-top-left-radius: 10px;
  border-bottom-left-radius: 10px;
}
.main__image img {
  height: 100%;
  object-fit: cover;
  object-position: left;
}
.main__intro {
  grid-row: 2/3;
  display: flex;
  flex-direction: column;
  gap: 1em;
}
.main__side {
  grid-column: 2/3;
  display: grid;
  grid-template-columns: auto 1fr auto;
  grid-template-rows: auto auto;
  grid-row: 3/4;
  row-gap: 2px;
  column-gap: 10px;
}
.main__socials {
  position: absolute;
  align-items: center;
  background-color: var(--clr-header);
  color: var(--clr-date);
  transform: scale(0);
  transition: 0.2s all ease-in-out;
  display: flex;
  gap: 15px;
  text-transform: uppercase;
  letter-spacing: 5px;
  right: -5.3em;
  bottom: 6.3em;
  border-radius: 10px;
  padding: 1em 2.5em;
}
.main__socials .links {
  display: flex;
  gap: 15px;
  align-items: baseline;
}
.main__socials::before {
  content: "";
  padding: 1em;
  position: absolute;
  z-index: 5;
  right: 7.5em;
  height: 40px;
  width: 40px;
  z-index: 100000;
  background: var(--clr-header);
  clip-path: polygon(65% 75%, 34% 45%, 93% 45%);
  bottom: -1.5em;
}

@media screen and (max-width: 39em) {
  .main {
    gap: 2em 2em;
    grid-template-columns: auto auto auto;
    grid-template-rows: 200px auto auto auto;
  }
  .main__image {
    grid-row: 1/2;
    grid-column: 1/-1;
    border-top-right-radius: 10px;
    border-bottom-left-radius: 0;
  }
  .main__image img {
    object-position: center -1px;
  }
  .main__intro {
    grid-row: 2/3;
    grid-column: 2/3;
  }
  .main__socials {
    position: static;
    grid-row: 3/5;
    grid-column: 1/-1;
    padding: 1em 2em;
    border-top-right-radius: 0px;
    border-top-left-radius: 0px;
  }
  .main__socials::before {
    content: none;
  }

  body {
    padding: 0 2.5em;
    align-items: center;
  }
}
.active {
  transform: scale(1);
}

.side {
  position: relative;
}
.side__profile-image, .side__share {
  grid-row: span 2;
  border-radius: 50%;
  overflow: hidden;
  transition: 0.3s all ease-in-out;
}
.side__profile-image {
  display: flex;
}
.side__profile-image img {
  max-width: 35px;
  object-fit: cover;
  object-position: center;
}
.side__share {
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 1em;
  background-color: var(--clr-background);
}
.side__share img {
  max-width: 35px;
}
.side__date {
  grid-row: 2;
  grid-column: 2/3;
}

.active-mobile {
  position: relative;
  transform: translateY(12px);
  background-color: var(--clr-intro);
  z-index: 1;
}
.active-mobile img {
  filter: brightness(0) invert(1);
}